import React from "react";
import Ninjas from "../components/ninjas";

class Start extends React.Component {
  state = {
    isCheck: false,
    NinjaA: false,
    NinjaB: false,
    NinjaC: false,
    ninjaA_Data: [
      {
        name: "당신은 개찐따같은 보통 닌자",
        id: 1,
        image:
          "data:image/jpeg;base64,",
      },
    ],
    ninjaB_Data: [
      {
        name: "당신은 군침이 싹 메마르는 건조 닌자",
        id: 2,
        image:
          "data:image/jpeg;base64,",
      },
    ],
    ninjaC_Data: [
      {
        name: "당신은 닌자 중의 닌자, 최고의 닌자, 킹왕짱 닌자",
        id: 3,
        image:
          "https://t1.daumcdn.net/liveboard/hero_nitko/8a4dcdc96e694a2b829c456706779b32.JPG",
      },
    ],
  };
  NinjaA = () => {
    this.setState({
      NinjaA: true,
      NinjaB: false,
      NinjaC: false,
      isCheck: true,
    });
    console.log(this.state);
  };
  NinjaB = () => {
    this.setState({
      NinjaA: false,
      NinjaB: true,
      NinjaC: false,
      isCheck: true,
    });
    console.log(this.state);
  };
  NinjaC = () => {
    this.setState({
      NinjaA: false,
      NinjaB: false,
      NinjaC: true,
      isCheck: true,
    });
    console.log(this.state);
  };

  render() {
    const {
      NinjaA,
      NinjaB,
      NinjaC,
      ninjaA_Data,
      ninjaB_Data,
      ninjaC_Data,
      isCheck,
    } = this.state;
    return (
      <div>
        <h1>빈칸에 들어갈 말을 고르시오</h1>
        <h3>
          나는 닌자다... 오늘도 나는 타겟을 암살하기 위해 타겟의 집에 몰래
          잠입했다.
        </h3>
        <h3>타겟의 방에 잠입성공..</h3>
        <h3>곤히 자고 있는 녀석의 목을 베려는 바로 그 순간!</h3>
        <h2>"야 이 새끼야 너 누구야!!!!!!!!!!"</h2>
        <h3>너무 놀란 나는 뒤를 돌아보았는데...!</h3>
        <h3>나를 바라보고 있던 그 놈은 바로바로..!</h3>
        <h1>[ ]!!!</h1>
        <button onClick={this.NinjaA}>타겟의 보디가드 닌자</button>
        <button onClick={this.NinjaB}>군침이 싹 도는 닌자루피</button>
        <button onClick={this.NinjaC}>
          방금 갓 데워진 따끈따끈한 햇반닌자
        </button>

        {isCheck ? (
          <div>
            <div>
              {NinjaA ? (
                <div>
                  {ninjaA_Data.map((ninja) => (
                    <Ninjas
                      name={ninja.name}
                      id={ninja.id}
                      key={ninja.id}
                      image={ninja.image}
                    />
                  ))}
                </div>
              ) : (
                <div></div>
              )}
            </div>
            <div>
              {NinjaB ? (
                <div>
                  {ninjaB_Data.map((ninja) => (
                    <Ninjas
                      name={ninja.name}
                      id={ninja.id}
                      key={ninja.id}
                      image={ninja.image}
                    />
                  ))}
                </div>
              ) : (
                <div></div>
              )}
            </div>
            <div>
              {NinjaC ? (
                <div>
                  {ninjaC_Data.map((ninja) => (
                    <Ninjas
                      name={ninja.name}
                      id={ninja.id}
                      key={ninja.id}
                      image={ninja.image}
                    />
                  ))}
                </div>
              ) : (
                <div></div>
              )}
            </div>
          </div>
        ) : (
          <div>
            <h1></h1>
          </div>
        )}
      </div>
    );
  }
}
export default Start;
